"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, LogOut, Loader2, BookOpen, Trophy, Sparkles } from "lucide-react"
import { StudyCard } from "@/components/study-card"
import { ProgressDashboard } from "@/components/progress-dashboard"
import { ThemeToggle } from "@/components/theme-toggle"
import { AddWordDialog } from "@/components/add-word-dialog"
import { useRouter } from "next/navigation"

type Word = {
  id: string
  original: string
  translation: string
  type: string
  tag: string
  easiness_factor: number
  interval: number
  repetitions: number
  next_review: string
}

export default function Dashboard() {
  const [words, setWords] = useState<Word[]>([])
  const [dueWords, setDueWords] = useState<Word[]>([]) 
  const [loading, setLoading] = useState(true)
  const [showAddDialog, setShowAddDialog] = useState(false)
  const [sessionCount, setSessionCount] = useState(0)

  const supabase = createClient()
  const router = useRouter()

  useEffect(() => {
    const fetchData = async () => {
      const { data: { user } } = await supabase.auth.getUser()
      if (!user) {
        router.push("/login")
        return
      }

      // Загружаем слова
      const { data: wordsData } = await supabase
        .from("words")
        .select("*")
        .order("created_at", { ascending: false })

      if (wordsData) {
        setWords(wordsData)
        const now = new Date()
        const due = wordsData.filter((w) => new Date(w.next_review) <= now)
        setDueWords(due)
      }
      setLoading(false)
    }
    fetchData()
  }, [router, supabase])

  const handleLogout = async () => {
    await supabase.auth.signOut()
    router.push("/login")
  }

  const handleWordAdded = (newWord: Word) => {
    setWords((prev) => [newWord, ...prev])
    if (new Date(newWord.next_review) <= new Date()) {
      setDueWords((prev) => [...prev, newWord])
    }
    setShowAddDialog(false)
  }

  const handleWordReviewed = async (id: string, quality: number) => {
    const word = words.find((w) => w.id === id)
    if (!word) return

    let { easiness_factor, interval, repetitions } = word
    if (quality >= 3) {
      if (repetitions === 0) interval = 1
      else if (repetitions === 1) interval = 6
      else interval = Math.round(interval * easiness_factor)
      repetitions += 1
      easiness_factor = Math.max(1.3, easiness_factor + (0.1 - (5 - quality) * (0.08 + (5 - quality) * 0.02)))
    } else {
      repetitions = 0
      interval = 1
    }

    const next_review = new Date()
    next_review.setDate(next_review.getDate() + interval)

    setDueWords((prev) => prev.filter((w) => w.id !== id))
    setSessionCount((prev) => prev + 1)
    setWords(prev => prev.map(w => w.id === id ? { ...w, easiness_factor, interval, repetitions, next_review: next_review.toISOString() } : w))

    await supabase.from("words").update({ easiness_factor, interval, repetitions, next_review: next_review.toISOString() }).eq("id", id)
  }

  const stats = {
    total: words.length,
    mastered: words.filter(w => w.interval > 21).length, 
  }

  const userStats = {
    dailyGoal: 20,
    dailyProgress: sessionCount, 
    streak: 1,
    totalReviews: sessionCount // Пока заглушка
  }

  if (loading) return <div className="min-h-screen flex items-center justify-center"><Loader2 className="h-8 w-8 animate-spin" /></div>

  return (
    <div className="min-h-screen bg-background text-foreground pb-20">
      {/* HEADER COMPACT */}
      <header className="sticky top-0 z-50 border-b bg-background/80 backdrop-blur">
        <div className="container max-w-5xl mx-auto h-14 flex items-center justify-between px-4">
          <div className="font-bold text-lg flex items-center gap-2">
            <BookOpen className="h-5 w-5 text-primary" />
            VocabApp
          </div>
          <div className="flex items-center gap-2">
            <ThemeToggle />
            <Button variant="ghost" size="icon" onClick={handleLogout}><LogOut className="h-4 w-4" /></Button>
          </div>
        </div>
      </header>

      <main className="container max-w-5xl mx-auto px-4 py-10 space-y-12">
        
        {/* 1. ЦЕНТРАЛЬНЫЙ ЗАГОЛОВОК (КАК НА v0) */}
        <div className="text-center space-y-4">
          <div className="flex items-center justify-center gap-3 mb-2">
             <BookOpen className="h-10 w-10 text-primary" />
             <h1 className="text-4xl font-extrabold tracking-tight lg:text-5xl">Vocabulary Learning</h1>
          </div>
          <p className="text-xl text-muted-foreground">Изучайте слова с интервальным повторением</p>
          <div className="pt-4">
             <Button onClick={() => setShowAddDialog(true)} size="lg" className="rounded-full px-8">
                <Plus className="mr-2 h-4 w-4" /> Добавить новое слово
             </Button>
          </div>
        </div>

        {/* 2. СЕТКА СТАТИСТИКИ */}
        <div className="max-w-4xl mx-auto">
           <ProgressDashboard stats={stats} userStats={userStats} />
        </div>

        {/* 3. ЗОНА КАРТОЧЕК */}
        <div className="max-w-xl mx-auto">
          {dueWords.length > 0 ? (
             <div className="bg-card border rounded-2xl shadow-sm overflow-hidden min-h-[400px] flex items-center justify-center p-6 relative">
                 <div className="absolute top-4 right-4 text-xs font-bold bg-secondary px-2 py-1 rounded">Очередь: {dueWords.length}</div>
                 <StudyCard word={dueWords[0]} onResult={(quality) => handleWordReviewed(dueWords[0].id, quality)} />
             </div>
          ) : (
            <Card className="text-center py-12 border-dashed bg-transparent shadow-none">
              <CardContent className="flex flex-col items-center gap-4">
                <div className="p-4 rounded-full bg-green-100 dark:bg-green-900/20">
                   <Trophy className="h-8 w-8 text-green-600 dark:text-green-500" />
                </div>
                <div className="space-y-1">
                   <h3 className="text-xl font-bold">Всё выучено! 🎉</h3>
                   <p className="text-muted-foreground">Слов для повторения пока нет.</p>
                </div>
              </CardContent>
            </Card>
          )}
        </div>

      </main>

      <AddWordDialog open={showAddDialog} onOpenChange={setShowAddDialog} onWordAdded={handleWordAdded} />
    </div>
  )
}